.container {
  display: flex;
  width: 100%;
}

.input {
  color: var(--input-color);
  width: 100%;
  line-height: 1.8em;
  border-radius: 0;

  &._underline {
    border-top-color: transparent;
    border-left-color: transparent;
    border-right-color: transparent;
    padding-left: 0;
    padding-right: 0;

    &:global.ant-input-number-focused,
    &:global.ant-input:focus,
    &:global.ant-input-focused {
      box-shadow: none;
    }
  }
}

.inputContainer {
  display: flex;
  flex-grow: 1;
}
