.container {
  padding: 40px 64px;
  display: flex;

  --table-title-lh: 20px;
  --table-title-mb: 16px;
}

.main {
  flex-grow: 1;
  flex-shrink: 0;
  margin-right: 64px;
}

.table {
  margin-bottom: 40px;
}

.tableTitle {
  font-size: 16px;
  line-height: var(--table-title-lh);
  font-weight: bold;
  margin-bottom: var(--table-title-mb);
}

.tableHeaderContainer {
  padding: 8px 16px;
  background-color: #dae8f7;
  font-weight: 500;

  > .tableHeaderCell {
    display: inline-block;
  }
}

.summaryContainer {
  position: relative;
  top: calc(var(--table-title-lh) + var(--table-title-mb));
}
