.label {
  font-size: 12px;
  color: #444444;
  display: block;
  margin-bottom: 8px;

  &:global._small {
    font-size: 10px;
  }

  &:global._inline {
    display: inline-block;
    margin-bottom: 0;
    margin-right: 12px;
  }

  &:global._required {
    span {
      position: relative;

      :after {
        content: '*';
        display: 'block';
        position: absolute;
        color: var(--danger-300);
        top: 0;
        right: -12px;
      }
    }
  }
}
